.About {
    display: flex;
    flex-direction: row-reverse;
    width: 100vw;
    height: 100vh;
    background-color: white;
}

.Title {
    font-family: 'Urbanist-Black';
    background-color: white;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: left;
    font-size: 3rem;
    padding-top: 6rem;
    padding-left: 6rem;

}

.contenido {
    width: 67%;
    height: auto;
    font-family: 'Montserrat-Regular';
    font-size: 1rem;
    letter-spacing: 0.03rem;
    padding-left: 6rem;
}

.contenedor-contenido {
    width: 50vw;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: left;
    padding-bottom: 6rem;
}

.Descripcion {
    width: 50vw;
    display: flex;
    flex-direction: column;
}

.Foto_desc {
    background-image: url('/public/fotos/fondo.jpg');
    background-size: cover;
    background-position: right center;
    width: 50vw;
    height: auto;
}

.contenedor-icon {
    position: relative;
    display: flex;
    flex-direction: row;
    top: 0;
    width: 100%;
    height: 70px;
    padding-top: 50px;
}

.contenedor-icon img {
    width: 1;
    margin-right: 40px;
    cursor: pointer;
    filter: invert(100%);
}



@media (max-width:768px) {

    .About {
        display: flex;
        flex-direction: column;
        width: 100vw;
        height: auto;
        background-color: white;
    }

    .Title {
        font-family: 'Urbanist-Black';
        background-color: white;
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        font-size: 12vw;
        padding-top: 30px;
        padding-bottom: 30px;
        padding-left: 0;
        height: auto;
    }

    .contenido {
        width: 100%;
        height: auto;
        font-family: 'Montserrat-Regular';
        font-size: .8em;
        letter-spacing: 0.03rem;
        padding: 0 5vw;

    }

    .contenedor-contenido {
        width: 100%;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
    }

    .Descripcion {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
    }

    .Foto_desc {
        background-image: url('/public/fotos/fondo.jpg');
        background-size: cover;
        background-position: right -85px center;
        width: 100%;
        height: 500px;
    }

    .contenedor-icon {
        position: relative;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        top: 0;
        width: 100%;
        height: auto;
        padding: 7vw 0;
    }

    .contenedor-icon img {
        width: 1;
        margin-right: 10px;
        margin-left: 10px;
        cursor: pointer;
        filter: invert(100%);
    }



}